import React from "react";
import {useState} from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import TextField from "@material-ui/core/TextField";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";

import Send from "@material-ui/icons/Send";

import emailjs from "emailjs-com";

const EMAILKEY=process.env.REACT_APP_EMAILJS_KEY
const SERVICE_ID= process.env.REACT_APP_TEMPLATE_KEY
const TEMPLATE_ID= process.env.REACT_APP_SERVICE_KEY




const useStyles = makeStyles((theme) => ({
  contactContainer: {
    background: "#233",
    height: "100vh",
  },
  heading: {
    color: "tomato",
    textAlign: "center",
    textTransform: "uppercase",
    marginBottom: "1rem",
  },
  form: {
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    position: "absolute",
  },
  input: {
    color: "#fff",
  },
  button: {
    marginTop: "1rem",
    color: "tomato",
    borderColor: "tan",
  },
  field: {
    margin: "1rem 0rem",
  },
}));  

const InputField = withStyles({
  root: {
    "& label.Mui-focused": {
      color: "tomato",
    },
    "& label": {
      color: "tan",
    },
    "& .MuiOutlinedInput-root": {
      "& fieldset": {
        borderColor: "tan",
      },
      "&:hover fieldset": {
        borderColor: "tan",
      },
      "&.Mui-focused fieldset": {
        color: "#fff",
        borderColor: "tan",
      },
    },
  },
})(TextField);

const Contact = () => {
const classes = useStyles();
const [fromEmail, setFromEmail] = useState("");
const [fromName, setFromName] = useState("");
const [toName, setToName] = useState("Santosh Kumar Behera");
const [message, setMessage] = useState("");
  return (
    <Box component="div" className={classes.contactContainer}>
      <Grid container justify="center">
        <Box component="form" className={classes.form}>
          <Typography variant="h5" className={classes.heading}>
            Contact me...
          </Typography>
          <InputField
            fullWidth={true}
            label="Name"
            value={fromName}
            onChange={(e) => setFromName(e.target.value)}
            variant="outlined"
            inputProps={{ className: classes.input }}
          />
          <InputField
            fullWidth={true}
            label="Email"
            value={fromEmail}
            onChange={(e) => setFromEmail(e.target.value)}
            variant="outlined"
            inputProps={{ className: classes.input }}
            className={classes.field}
          />
          <InputField
            fullWidth={true}
            label="Message"
            variant="outlined"
            value= {message}
            onChange={(e) => setMessage(e.target.value)}
            multiline
            rows={4}
            inputProps={{ className: classes.input }}
          />
          <Button
            variant="outlined"
            fullWidth={true}
            endIcon={<Send />}
            onClick={() => {
              emailjs.send(SERVICE_ID, TEMPLATE_ID, {'from_name':fromName, 'from_email':fromEmail, 'to_name':toName, 'message':message}, EMAILKEY)
              .then(function(response) {
                console.log('SUCCESS!', response.status, response.text);
             }, function(error) {
                console.log('FAILED...', error);
             })}}
            className={classes.button}
          >
            Contact Me
          </Button>
        </Box>
      </Grid>
    </Box>
  );
};

export default Contact;
